import React, { useState } from 'react';
import { Container, Typography, Box, Button, LinearProgress, Alert, Paper, List, ListItem, ListItemIcon, ListItemText } from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import ErrorIcon from '@mui/icons-material/Error';
import { SnackbarProvider, useSnackbar } from 'notistack';
import './App.css';

function App() {
  const [file, setFile] = useState(null);
  const [status, setStatus] = useState('');
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);
  const { enqueueSnackbar } = useSnackbar();

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
    setStatus('');
    setResults([]);
  };

  const handleSend = async () => {
    if (!file) {
      setStatus('Please select an Excel file.');
      return;
    }
    setLoading(true);
    setStatus('Uploading and sending messages...');
    const formData = new FormData();
    formData.append('file', file);
    try {
      const response = await fetch('http://localhost:8000/send-whatsapp', {
        method: 'POST',
        body: formData,
      });
      const data = await response.json();
      console.log('data', data)
      if (data.results) {
        setResults(data.results);
        setStatus('Messages sent!');
        // Show toast for each result
        data.results.forEach((r) => {
          if (r.status === 'sent') {
            enqueueSnackbar(`Message sent to ${r.to}`, { variant: 'success', autoHideDuration: 3000 });
          } else {
            enqueueSnackbar(`Failed to send to ${r.to}${r.error ? ': ' + r.error : ''}`, { variant: 'error', autoHideDuration: 3000 });
          }
        });
      } else if (data.status) {
        setStatus(data.status);
        setResults([]);
        enqueueSnackbar(data.status, { variant: 'info', autoHideDuration: 3000 });
      } else {
        setStatus('Error: ' + (data.error || 'Unknown error'));
        enqueueSnackbar('Error: ' + (data.error || 'Unknown error'), { variant: 'error', autoHideDuration: 3000 });
      }
    } catch (err) {
      setStatus('Error: ' + err.message);
      enqueueSnackbar('Error: ' + err.message, { variant: 'error', autoHideDuration: 3000 });
    }
    setLoading(false);
  };

  return (
    <Container maxWidth="sm" sx={{ minHeight: '100vh', display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
      <Paper elevation={3} sx={{ p: 4, mt: 6, mb: 6 }}>
        <Box display="flex" flexDirection="column" alignItems="center" gap={2}>
          <Typography variant="h4" fontWeight={700} color="primary" gutterBottom>
            Bulk WhatsApp Sender
          </Typography>
          <Button
            variant="contained"
            component="label"
            startIcon={<CloudUploadIcon />}
            sx={{ width: '100%', mb: 2 }}
          >
            {file ? file.name : 'Upload Excel File'}
            <input type="file" accept=".xlsx,.xls" hidden onChange={handleFileChange} />
          </Button>
          <Button
            variant="contained"
            color="success"
            startIcon={<WhatsAppIcon />}
            onClick={handleSend}
            sx={{ width: '100%' }}
            disabled={loading}
          >
            Send WhatsApp
          </Button>
          {loading && <LinearProgress sx={{ width: '100%', mt: 2 }} />}
          {status && (
            <Alert severity={status.startsWith('Error') ? 'error' : 'info'} sx={{ width: '100%', mt: 2 }}>
              {status}
            </Alert>
          )}
          {results.length > 0 && (
            <Box sx={{ width: '100%', mt: 3 }}>
              <Typography variant="h6" gutterBottom>Results:</Typography>
              <List>
                {results.map((r, i) => (
                  <ListItem key={i}>
                    <ListItemIcon>
                      {r.status === 'sent' ? <CheckCircleIcon color="success" /> : <ErrorIcon color="error" />}
                    </ListItemIcon>
                    <ListItemText
                      primary={`${r.to}: ${r.status}`}
                      secondary={r.sid ? `SID: ${r.sid}` : r.error ? r.error : ''}
                      primaryTypographyProps={{ color: r.status === 'sent' ? 'green' : 'red' }}
                    />
                  </ListItem>
                ))}
              </List>
            </Box>
          )}
        </Box>
      </Paper>
    </Container>
  );
}

// Wrap the App component with SnackbarProvider
export default function AppWithSnackbar() {
  return (
    <SnackbarProvider maxSnack={3} anchorOrigin={{ vertical: 'top', horizontal: 'right' }}>
      <App />
    </SnackbarProvider>
  );
}
